import React, { Component } from 'react'

// task „ÇíÁ∑®ÈõÜ„Åó„Åü„Å®„Åç„ÅÆ„ÄÅsave / cencel „Éú„Çø„É≥
export default class TaskButtons extends Component {
  constructor(props) {
    super(props)

    Object.getOwnPropertyNames(this.__proto__).forEach(func =>
      this[func] = this[func].bind(this)
    )
  }

  // „Çø„Çπ„ÇØ„ÇíÊõ¥Êñ∞„Åó„Åü
  handleUpdated() {
    this.props.updatedTask(this.props.id)
  }

  // „Çø„Çπ„ÇØ„ÇíÁ∑®ÈõÜ„Åó„Åü„Åå„ÄÅ„Ç≠„É£„É≥„Çª„É´„Åó„Åü
  handleDidNotUpdate() {
    this.props.didNotUpdateTask(this.props.id)
  }

  render() {
    return (
      <div className="task-buttons" data-editing={this.props.editing}>
        <button className="task-buttons__button task-buttons__button__save" onClick={this.handleUpdated} title="Save" type="button">
          <span role="img" aria-label="save">üôÜ‚Äç‚ôÇÔ∏è</span> save
        </button>

        <button className="task-buttons__button task-buttons__button__cancel" onClick={this.handleDidNotUpdate} title="Cancel" type="button">‚Äç
          <span role="img" aria-label="cancel">üôÖ‚Äç‚ôÇÔ∏è</span> cancel
        </button>
      </div>
    )
  }
}
